@font-face {
  font-family: "SailingClub";
  src: url(../../fonts/SailingClub/SailingClub-Regular.ttf);
  font-display: swap;
}
@font-face {
  font-family: "iCielGotham";
  src: url(../../fonts/iCielGotham/iCielGothamMediumRegular.otf);
  font-display: swap;
}
@font-face {
  font-family: "Gotham";
  src: url(../../fonts/Gotham/GothamLight.otf);
  font-display: swap;
}
/** Media Queries **/
html {
  overflow-x: hidden;
}

body {
  font-size: 16px;
  background: #FAFBFB;
}

picture {
  display: block;
}

.w-100, .wh-100 {
  width: 100%;
}

.h-100, .wh-100 {
  height: 100%;
}

img {
  vertical-align: top;
  height: auto;
}

.relative {
  position: relative;
}

.absolute {
  position: absolute;
}

a, button, input[type=submit] {
  cursor: pointer;
  transition: all 0.3s ease;
}

main, section, header, footer, aside, nav {
  width: 100%;
}

a {
  text-decoration: none;
}

textarea, button, input, select, option {
  outline: none;
}

.container {
  margin-inline: auto;
}
@media (min-width: 1200px) {
  .container {
    width: 100%;
    max-width: 88.8888888889vw;
  }
}
@media (max-width: 1200px) {
  .container {
    width: 92%;
  }
}

.d-block, a {
  display: block;
}

video {
  display: block;
  width: 100%;
  aspect-ratio: 16/9;
}

h1, h2, h3, h4, h5, h6 {
  margin: 0;
}

